}
}
@keyframes kf-menu {
from {
    opacity: 0;
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
}
to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
@-webkit-keyframes kf-menu {
  /* Safari 和 Chrome */
from {
    opacity: 0;
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
}
to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
