.post_animate[data-v-54bda51a] {
  opacity: 0;
  animation: kf-menu 1s;
  -moz-animation: kf-menu 1s;
  /* Firefox */
  -webkit-animation: kf-menu 1s;
  /* Safari 和 Chrome */
  -o-animation: kf-menu 1s;
  animation-fill-mode: forwards;
  -moz-animation-fill-mode: forwards;
  /* Firefox */
  -webkit-animation-fill-mode: forwards;
  /* Safari 和 Chrome */
  -o-animation-fill-mode: forwards;
  animation-delay: 1s;
  -moz-animation-delay: 1s;
  /* Firefox */
  -webkit-animation-delay: 1s;
  /* Safari 和 Chrome */
  -o-animation-delay: 1s;
}



