
@import '/static/bower_components/font-awesome/css/font-awesome.min.css';
@import '/static/bower_components/animate.css/animate.min.css';
html,
body {
  margin: 0;
  padding: 0;
  font-family: 'Neucha', "PingFang SC", "Microsoft YaHei", sans-serif;
  font-size: 14px;
  line-height: 2;
  color: #555;
  background: #fff;
  position: relative;
  height: 100%;
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  font-weight: bold;
  line-height: 1.5;
  font-family: 'Neucha', "PingFang SC", "Microsoft YaHei", sans-serif;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
ul {
  margin: 0;
  padding: 0;
}
ul,
li {
  list-style: none;
  float: left;
  color: black;
  margin: 0;
  padding: 0;
}
a {
  color: #555;
  text-decoration: none;
  word-wrap: break-word;
}
a,
button {
  transition: all 0.3s ease;
}
@media (min-width: 1600px) {
body {
    font-size: 16px;
}
}
.main {
  padding-bottom: 100px;
}
.main-inner {
  margin: 0 auto;
  width: 700px;
  margin-top: 80px;
}
@media (min-width: 1600px) {
.container .main-inner {
    width: 900px;
}
}



